import React from 'react'

const page = () => {
  return (
   <>
    <section className='bg-purple-300 min-h-[100vh] py-6'>
      <div className='h-[650px] w-full md:w-3xl mx-auto px-4'>
       <h1 className='text-center text-black py-7 text-4xl font-bold'>Edit your profile</h1>
       <div className='flex flex-col gap-1 mb-9'>
        <label className='text-black font-bold mx-3' htmlFor="">Handel</label>
        <input className='bg-white text-black w-full rounded-2xl px-7 py-2' type="text" placeholder='Enter your name' />
       </div>
       <div className='flex flex-col gap-1 mb-9'>
        <label className='text-black font-bold mx-3' htmlFor="">Add links</label>
        <div className='flex gap-1.5 mb-4'>
        <input className='bg-white text-black w-full rounded-2xl px-7 py-2' type="text" placeholder='Site Name' />
        <input className='bg-white text-black w-full rounded-2xl px-7 py-2' type="text" placeholder='Site link' />
        </div>
        <button className="bg-gray-800 hover:bg-gray-700 py-2 px-7 mx-auto cursor-pointer text-white rounded-2xl">Add more Links</button>
       </div>
       <div className='flex flex-col gap-1 mb-9'>
        <label className='text-black font-bold mx-3' htmlFor="">Profile picture</label>
        <input className='bg-white text-black w-full rounded-2xl px-7 py-2' type="text" placeholder='Enter your pofile pic' />
       </div>
       <div className='w-full flex justify-center '>
       <button className="bg-gray-800 hover:bg-gray-700 mt-10 py-2 px-7 text-lg mx-auto cursor-pointer text-white rounded-2xl">Save profile</button>
       </div>
      </div>
    </section>
   </>
  )
}

export default page
